.mp_pprofile {
  margin: -1rem;
  font-size: 14px;
  // position: relative;
}
.mp_pprofile .p-header {
  padding: 2rem 2rem 0 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
.mp_pprofile .p-avatar {
  position: relative;
  width: 75px;
  height: 75px;
  border-radius: 50%;
}
.mp_pprofile .p-avatar > img {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}
.mp_pprofile .p-overlay-loading {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba($color: #fff, $alpha: 0.5);
}
.mp_pprofile .p-overlay-loading.rounded {
  border-radius: 50%;
}
.mp_pprofile .p-overlay-loading > svg {
  fill: var(--color-primary);
  animation: loading 2s linear infinite;
}
.mp_pprofile .p-upload {
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: #fff;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mp_pprofile .p-upload > input {
  display: none;
}
.mp_pprofile .p-upload > label {
  color: var(--color-primary);
  cursor: pointer;
}
.mp_pprofile .p-row-dFaCjFs {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}
.mp_pprofile .p-short-info {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}
.mp_pprofile .p-short-info .name {
  font-weight: 700;
  font-size: 16px;
}
.mp_pprofile .tabs {
  margin-top: 3rem;
}
.mp_pprofile .tab-item {
  color: rgba($color: #fff, $alpha: 0.5);
  padding: 0.5rem 0;
  margin-right: 2rem;
  border-bottom: 3px solid transparent;
  cursor: pointer;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  transition: 0.2s all ease;
}
.mp_pprofile .tab-item.active {
  color: rgba($color: #fff, $alpha: 1);
  border-color: #fff;
}
.mp_pprofile .p-content {
  padding: 2rem;
  background-color: #fff;
}
.mp_pprofile .form-control {
  font-size: 14px;
  border: 2px solid var(--color-primary-overlay);
  background-color: var(--color-primary-overlay);
}
.mp_pprofile .form-control:focus {
  box-shadow: none;
  border-color: var(--color-primary);
  background-color: #fff;
}
.mp_pprofile .p-content .my-form {
  margin-top: 1rem;
}
.mp_pprofile .v1_lable_control_inline {
  padding: 1rem 0;
  margin-bottom: 0;
  border-top: 1px solid var(--color-divider);
}
.mp_pprofile .v1_lable_control_inline .label {
  font-weight: 600;
  width: 200px;
}
.mp_pprofile .p-title-tab {
  font-size: 15px;
}
.mp_pprofile .p-row-dFaCjFe {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
}
.mp_pprofile .btn {
  font-size: 14px;
}
.mp_pprofile .btn-esc-info {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  color: #fff;
}
.mp_pprofile .btn-esc-info:focus {
  box-shadow: var(--box-shadow-focus);
}
.mp_pprofile .form-control:disabled {
  background-color: #f0f8ff;
}
@keyframes loading {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  .mp_pprofile .v1_lable_control_inline .label {
    padding: 0.375rem 0.75rem;
  }
}
